"use client"

import { useState, useEffect } from "react"
import Image from "next/image"
import { DashboardLayout } from "@/components/layout/DashboardLayout"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { 
  MicIcon, 
  MicOffIcon, 
  HeadphonesIcon,
  UsersIcon,
  PlusIcon
} from "lucide-react"
import { toast } from "sonner"

interface Participant {
  id: string
  name: string
  image: string
  isSpeaking: boolean
  isMuted: boolean
}

interface ConferenceRoom {
  id: string
  name: string
  participants: Participant[]
  isActive: boolean
}

const mockRooms: ConferenceRoom[] = [
  {
    id: "1",
    name: "German Conversation Practice",
    participants: [
      {
        id: "1",
        name: "Olivia",
        image: "/artifacts/student_monitoring_photos.jpeg",
        isSpeaking: true,
        isMuted: false
      },
      {
        id: "2",
        name: "James",
        image: "/artifacts/student_monitoring_photos.jpeg",
        isSpeaking: false,
        isMuted: true
      },
      {
        id: "3",
        name: "William",
        image: "/artifacts/student_monitoring_photos.jpeg",
        isSpeaking: false,
        isMuted: false
      }
    ],
    isActive: true
  },
  {
    id: "2",
    name: "Arabic Pronunciation Group",
    participants: [
      {
        id: "4",
        name: "Emma",
        image: "/artifacts/student_monitoring_photos.jpeg",
        isSpeaking: false,
        isMuted: false
      }
    ],
    isActive: true
  }
]

export default function ConferencingPage() {
  const [rooms, setRooms] = useState<ConferenceRoom[]>(mockRooms)
  const [activeRoom, setActiveRoom] = useState<ConferenceRoom | null>(null)
  const [isMuted, setIsMuted] = useState(false)

  const joinRoom = (room: ConferenceRoom) => {
    setActiveRoom(room)
    toast.success(`Joined ${room.name}`)
  }

  const leaveRoom = () => {
    setActiveRoom(null)
    toast.info("Left the conference room")
  }

  const toggleMute = () => {
    setIsMuted(!isMuted)
    toast.success(isMuted ? "Microphone unmuted" : "Microphone muted")
  }

  return (
    <DashboardLayout>
      <div className="space-y-6">
        {activeRoom ? (
          <Card className="p-6">
            <div className="flex items-center justify-between mb-6">
              <div>
                <h2 className="text-2xl font-semibold">{activeRoom.name}</h2>
                <p className="text-gray-500">
                  {activeRoom.participants.length} Participants
                </p>
              </div>
              <div className="flex space-x-2">
                <Button
                  variant="outline"
                  size="icon"
                  onClick={toggleMute}
                >
                  {isMuted ? (
                    <MicOffIcon className="h-5 w-5" />
                  ) : (
                    <MicIcon className="h-5 w-5" />
                  )}
                </Button>
                <Button
                  variant="destructive"
                  onClick={leaveRoom}
                >
                  Leave Room
                </Button>
              </div>
            </div>

            <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
              {activeRoom.participants.map((participant) => (
                <Card key={participant.id} className="p-4">
                  <div className="relative">
                    <div className="relative h-32 w-32 mx-auto rounded-full overflow-hidden mb-4">
                      <Image
                        src={participant.image}
                        alt={participant.name}
                        fill
                        className="object-cover"
                      />
                    </div>
                    {participant.isSpeaking && (
                      <Badge className="absolute -top-2 right-1/4 bg-green-500">
                        Speaking
                      </Badge>
                    )}
                  </div>
                  <div className="text-center">
                    <h3 className="font-medium">{participant.name}</h3>
                    {participant.isMuted && (
                      <MicOffIcon className="h-4 w-4 mx-auto mt-2 text-gray-400" />
                    )}
                  </div>
                </Card>
              ))}
            </div>
          </Card>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            {rooms.map((room) => (
              <Card key={room.id} className="p-6">
                <div className="flex items-center justify-between mb-4">
                  <div>
                    <h3 className="text-xl font-semibold">{room.name}</h3>
                    <p className="text-gray-500">
                      {room.participants.length} Participants
                    </p>
                  </div>
                  <Badge variant={room.isActive ? "default" : "secondary"}>
                    {room.isActive ? "Active" : "Inactive"}
                  </Badge>
                </div>
                <div className="flex -space-x-2 mb-4">
                  {room.participants.map((participant) => (
                    <div
                      key={participant.id}
                      className="relative h-8 w-8 rounded-full overflow-hidden border-2 border-white"
                    >
                      <Image
                        src={participant.image}
                        alt={participant.name}
                        fill
                        className="object-cover"
                      />
                    </div>
                  ))}
                </div>
                <Button
                  className="w-full"
                  onClick={() => joinRoom(room)}
                >
                  <HeadphonesIcon className="h-4 w-4 mr-2" />
                  Join Room
                </Button>
              </Card>
            ))}
            <Card className="p-6 flex items-center justify-center border-dashed">
              <Button variant="outline" className="flex items-center">
                <PlusIcon className="h-4 w-4 mr-2" />
                Create New Room
              </Button>
            </Card>
          </div>
        )}
      </div>
    </DashboardLayout>
  )
}
